.c
{
    padding: 10px;
    text-wrap: wrap;
}
img
{
    margin: 0px 150px 0px 0px;
    height: 180px;
    width: 180px;
    object-fit: cover;
    border-radius: 50%;
    position: relative;
    z-index: 2;
}
.circle
{
    height: 180px;
    width: 180px;
    border-radius: 50%;
    position: absolute;
    background-color: darkorchid;
    z-index: -1;
    padding: 0px 0px 0px 10px;

}

h4
{
    color: darkorchid;
}

.flex
{
    display: flex;
}
.flex .name
{
    display: flex;
    flex-direction: column;
    height: 100px;
    align-items: center;
    

}
.other
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.other .icon
{
    margin: 0px 20px 10px 0px;
}

.other .icon button
{
    height: 30px;
    width: 30px;
    margin: 0px 10px;
    color: darkorchid;
    background-color: transparent;
    font-size: 30px;
    font-weight: bold;
    border: none;
}

.bt
{
    height: 35px;
    width: 140px;
    border-radius: 10px;
    border: none;
    background-color: darkorchid;
    color: white;
    font-weight: bold;
    cursor: pointer;
}

.left, .right
{
    text-align: center;
}

.flex2 
{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
}